/**
 * 
 */
function initStatusBar() {
    if (statusBarApp)
        return;
    try {
        statusBarContainer = createScriptIdDiv();
        statusBarContainer.css({
            position: 'fixed',
            top: '0',
            left: '0',
            width: '100%',
            height: '100%',
            zIndex: '9999',
            pointerEvents: 'none',
        });
        $('body').append(statusBarContainer);
        const app = createApp(StatusBarWrapper);
        teleportStyle();
        statusBarApp = app;
        app.mount(statusBarContainer[0]);
        console.info('[] ');
    }
    catch (error) {
        console.error('[] :', error);
    }
}
/**
 * 
 */
function toggleStatusBar() {
    console.info('[] :', statusBarVisible);
    if (!statusBarApp) {
        console.info('[] ...');
        initStatusBar();
        // 
        setTimeout(() => {
            const state = window.__statusBarState;
            if (state && state.toggle) {
                state.toggle();
                statusBarVisible = state.isVisible.value;
            }
            else {
                statusBarVisible = !statusBarVisible;
            }
            console.info('[] :', statusBarVisible);
        }, 300);
        return;
    }
    // 
    const state = window.__statusBarState;
    if (state && state.toggle) {
        state.toggle();
        statusBarVisible = state.isVisible.value;
    }
    else {
        statusBarVisible = !statusBarVisible;
    }
    console.info('[] :', statusBarVisible);
}
/**
 * 
 */
$(window).on('pagehide', () => {
    toastr.info('', '', {
        timeOut: 2000,
        progressBar: true,
    });
    // 
    if (statusBarApp) {
        statusBarApp.unmount();
        statusBarApp = null;
    }
    if (statusBarContainer) {
        statusBarContainer.remove();
        statusBarContainer = null;
    }
    destroyScriptIdDiv();
    deteleportStyle();
});
console.info('');
